<template>
  <div class="register-container">
    <div class="header" @click="toWelcome">
      <img src="../assets/homeLogo.jpg" style="width: 230px; height: 54px;">
    </div>
    <div class="register-body">
      <el-form class="register-form" ref="registerFormRef" :model="registerForm" :rules="registerFormRules" label-width="80px">
        <el-form-item label="" prop="userName">
          <el-input v-model="registerForm.userName" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="registerForm.password" type="password" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="confirmPassword">
          <el-input v-model="registerForm.confirmPassword" type="password" placeholder="" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="phoneNumber">
          <el-input v-model="registerForm.phoneNumber" type="text" aria-placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="gender" label="1"></el-radio>
          <el-radio v-model="gender" label="0"></el-radio>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="register"></el-button>
        </el-form-item>
        <el-form-item id="protocal">
          <el-link type="primary" style="line-height: 14px;">NN</el-link>
          <el-link type="primary" style="line-height: 14px;">XX</el-link>
        </el-form-item>
      </el-form>
      <img src="../assets/register.png">
    </div>
    <div class="footer-mini"></div>
    <div class="footer">
      <img style="width: 512px; height: 70px" src="../assets/register-footer.jpg">
    </div>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    let validatePass = (rule, value, callback) => {
      if (value === '') {
        callback(new Error(''))
        // password 
      } else if (value !== this.registerForm.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }
    let checkMobile = (rule, value, cb) => {
      const regMobile = /^(0|86|17951)?(13[0-9]|15[0123456789]|17[678]|18[0-9]|14[57])[0-9]{8}$/
      if (regMobile.test(value)){
        return cb()
      }
      cb(new Error(''))
    }
    return {
      gender: '1',
      registerForm: {
        userName: '',
        password: '',
        confirmPassword: '',
        phoneNumber: '',
        sex: ''
      },
      registerFormRules: {
        userName: [
          { required: true, message: "", trigger: "blur"},
          { min: 2, max: 20, message: "220", trigger: "blur"}
        ],
        password: [
          { required: true, message: "", trigger: "blur"},
          { min:6, max: 16, message: "616", trigger: "blur"}
        ],
        confirmPassword: [
          { required: true, validator: validatePass, message: "", trigger: "blur"},
        ],
        phoneNumber: [
          { required: true, message: '', trigger: 'blur' },
          { validator: checkMobile, trigger: 'blur'}
        ]
      }
    }
  },
  methods: {
    register() {
      this.$refs.registerFormRef.validate(async valid => {
        if(!valid) return
        this.registerForm.sex = this.gender === '1'
        axios.defaults.headers.post['Content-Type'] = 'application/json'
        const { data: res} = await axios.post('sysUser/register', JSON.stringify(this.registerForm));
        if(res.code !== 200) return this.$message.error(res.msg);

        this.$message.success("");
        this.$router.push('/login')
        this.$refs.registerFormRef.resetFields()
      })
    },
    toWelcome() {
      this.$router.push('/welcome')
    }
  }
}
</script>

